import React from 'react';
import { Component } from 'react';
import Header from "../Header";
import BoatInfo from "./BoatInfo";
import DetailsGroup from "./DetailsGroup";
import BoatPropertyProgress from './BoatPropertyProgress';
import PropertyInfo from './PropertyInfo';
import AvatarComponent from '../AvatarComponent';
import MenuComponent from '../MenuComponent';

import Tooltip from './Tooltip';

import "../../Profile.scss";



class BoatConstructor extends Component {

    constructor(props) {
        super(props);

        this.state = { details: [], activeBoat: '', activeProducer: { details: [] }, speed: '', boost: '', maxWeight: '', control: '', weight: '', price: '', boats: [], producers: [] }

        this.getProducers();
        this.getBoats();

        this.countProperties(this.state.activeBoat);

    }

    getProducers = () => {
        fetch('/webApp/commands/producers/all', {
            method: 'GET'
        })
            .then((response) => {
                if (!response.ok)
                    this.handleChange('error', '–û—à–∏–±–∫–∞: –Ω–µ–≤–µ—Ä–Ω—ã–π –æ—Ç–≤–µ—Ç –æ—Ç —Å–µ—Ä–≤–µ—Ä–∞.');
                else {
                    response.json().then((resp) => {
                        this.state.producers = resp;
                        this.state.activeProducer = resp[0];
                        console.log("Got producers", resp);
                        console.log("Active producer now:", this.state.activeProducer);
                        this.forceUpdate();
                    }).catch((err) => {
                        console.log(err);
                    });
                }
            })
            .catch((error) => {
                alert('–ù–µ –ø–æ–ª—É—á–∏–ª–æ—Å—å –æ—Ç–ø—Ä–∞–≤–∏—Ç—å –∑–∞–ø—Ä–æ—Å: ' + error.statusText);
            });
    }

    getBoats = () => {
        fetch('/webApp/commands/producers/boats/all', {
            method: 'GET'
        })
            .then((response) => {
                if (!response.ok)
                    this.handleChange('error', '–û—à–∏–±–∫–∞: –Ω–µ–≤–µ—Ä–Ω—ã–π –æ—Ç–≤–µ—Ç –æ—Ç —Å–µ—Ä–≤–µ—Ä–∞.');
                else {
                    response.json().then((resp) => {
                        this.state.boats = resp;
                        this.state.activeBoat = resp[0];
                        console.log("Got boats:", resp);
                        console.log("Active boat now:", this.state.activeBoat);
                        this.countProperties(this.state.activeBoat);
                        this.forceUpdate();
                    }).catch((err) => {
                        console.log(err.message);
                    });
                }
            })
            .catch((error) => {
                alert('–ù–µ –ø–æ–ª—É—á–∏–ª–æ—Å—å –æ—Ç–ø—Ä–∞–≤–∏—Ç—å –∑–∞–ø—Ä–æ—Å: ' + error.statusText);
            });
    }

    addDetail = (detail) => {

        let types =[]

        for(let i =0; i < this.state.details.length; i++)
            types.push(this.state.details[i].type);

        if (types.indexOf(detail.type) === -1) {
            this.state.details.push(detail);
            this.countProperties(this.state.activeBoat);
            this.forceUpdate();
        }
    }

    deleteDetail = (index) => {
        this.state.speed = -this.state.details[index].speedEffect + +this.state.speed;
        this.state.boost = -this.state.details[index].boostEffect + +this.state.boost;
        this.state.control = -this.state.details[index].controllabilityEffect + +this.state.control;
        this.state.weight = -this.state.details[index].weight + +this.state.weight;
        this.state.price = -this.state.details[index].price + +this.state.price;

        this.state.details.splice(index, 1);
        this.forceUpdate();
    }

    createDetailsGroupsFromProducer = (producer) => {
        let groupsNames = [];

        for (let i = 0; i < producer.details.length; i++)
            if (groupsNames.indexOf(producer.details[i].type) === -1)
                groupsNames.push(producer.details[i].type);

        let detailsGroups = [];

        for (let i = 0; i < groupsNames.length; i++) {
            let details = [];
            for (let j = 0; j < producer.details.length; j++)
                if (groupsNames[i] == producer.details[j].type) {
                    details.push(producer.details[j]);
                }

            console.log("Details in group " + groupsNames[i], details);

            let detailsPics = []

            details.forEach(detail => {
                let boostEffect1 = detail.boostEffect;
                let controllabilityEffect1 = detail.controllabilityEffect;
                let speedEffect1 = detail.speedEffect;
                let price1 = detail.price;
                let weight1 = detail.weight;

                let detailCard = 
                <button onClick={() => this.addDetail(detail)} style= {{border: "0", padding: "0", background–°olor: "rgba(255,255,255,0)"}}>
                    <figure class="image is-128x128"><AvatarComponent userid={"/webApp/detail_" + detail.id + ".png"} />
                    </figure> 
                       <Tooltip boostEffect={boostEffect1} controllabilityEffect={controllabilityEffect1} speedEffect={speedEffect1} price={price1} weight={weight1}> INFO</Tooltip>
                    </button>;

                detailsPics.push(detailCard);

            });

            detailsGroups.push(<div className="box">
                <p class="title is-3">{groupsNames[i]}</p>
                <div class="tile is-ancestor">
                    {detailsPics}
                </div>
            </div>);
        }

        return detailsGroups;
    }

    handleBoatChange = (e) => {
        let selectItem = document.getElementById("boatSelect");
        let value = selectItem.options[selectItem.selectedIndex].value;
        console.log("change boat:", value);
        this.setState({ activeBoat: this.state.boats[value] });
        this.countProperties(this.state.boats[value]);
        this.forceUpdate();
    }

    handleProducerChange = (e) => {
        let selectItem = document.getElementById("producerSelect");
        let value = selectItem.options[selectItem.selectedIndex].value;
        console.log("change producer", value);
        this.setState({ activeProducer: this.state.producers[value] });
        this.forceUpdate();
    }

    createBoatsOptions = () => {
        let options = [];

        for (let i = 0; i < this.state.boats.length; i++) {
            options.push(<option value={i}>{this.state.boats[i].type}</option>);
            console.log("Option boat:", this.state.boats[i].type);
        }
        return options;
    }

    createProducersOptions = () => {
        let options = [];

        for (let i = 0; i < this.state.producers.length; i++) {
            options.push(<option value={i}>{this.state.producers[i].name}</option>);
            console.log("Option producer:", this.state.producers[i].name);
        }

        return options;
    }

    countProperties = (activeBoat) => {

        this.state.maxWeight = 0;
        this.state.speed = 0;
        this.state.boost = 0;
        this.state.control = 0;
        this.state.weight = 0;
        this.state.price = 0;


        for (let i = 0; i < this.state.details.length; i++) {
            let detail = this.state.details[i];
            this.state.speed = +detail.speedEffect + +this.state.speed;
            this.state.boost = +detail.boostEffect + +this.state.boost;
            this.state.control = +detail.controllabilityEffect + +this.state.control;
            this.state.weight = +detail.weight + +this.state.weight;
            this.state.price = +detail.price + +this.state.price;
        }
        this.state.maxWeight = activeBoat.loadCapacity;
        this.state.speed = +activeBoat.maxSpeed + +this.state.speed;
        this.state.boost = +activeBoat.boost + +this.state.boost;
        this.state.control = +activeBoat.controllability + +this.state.control;
        this.state.weight = +activeBoat.weight + +this.state.weight;
        this.state.price = +activeBoat.price + +this.state.price;
    }

    createDetailsTags = () => {
        let tags = [];

        for (let i = 0; i < this.state.details.length; i++) {
            tags.push(<span class="tag is-warning is-medium">{this.state.details[i].type}<button class="delete is-small" onClick={() => this.deleteDetail(i)}></button></span>);
        }
        console.log(tags);
        return tags;
    }

    render() {
        console.log("active boat", this.state.activeBoat);

        document.title = "Boat constructor page";

        return (
            <div>
                <MenuComponent />
                <div class="is-color2 " id="boatConstructorMain">

                    <div class="tile is-ancestor">
                        <div class="tile is-4 is-color3-light is-vertical">
                            <h1 class="title is-1">–õ–æ–¥–∫–∞</h1>
                            <div class="field">
                                <div class="control">
                                    <div class="select is-medium">
                                        <select id="boatSelect" onChange={this.handleBoatChange}>
                                            {this.createBoatsOptions()}
                                        </select>
                                    </div>

                                </div>
                            </div>
                            <div>
                                <div >
                                    <figure class="image is-4by3">
                                        <AvatarComponent userid={"/webApp/boat_" + this.state.activeBoat.id + ".png"} />
                                    </figure>
                                </div>
                                <div >
                                    <h1 class="title is-1">–•–∞—Ä–∞–∫—Ç–µ—Ä–∏—Å—Ç–∏–∫–∏</h1>
                                    <div className="box">
                                        <p class="title is-3">–ú–∞–∫—Å–∏–º–∞–ª—å–Ω–∞—è —Å–∫–æ—Ä–æ—Å—Ç—å</p>
                                        <p class="subtitle is-5">{this.state.speed}/1500</p>
                                        <progress class="progress is-info" value={this.state.speed} max={1500} min={0}>{this.state.speed}</progress>
                                    </div>
                                    <div className="box">
                                        <p class="title is-3">–£—Å–∫–æ—Ä–µ–Ω–∏–µ</p>
                                        <p class="subtitle is-5">{this.state.boost} /500</p>
                                        <progress class="progress is-info" value={this.state.boost} max={500} min={0}>{this.state.boost} </progress>
                                    </div>
                                    <div className="box">
                                        <p class="title is-3">–ì—Ä—É–∑–æ–ø–æ–¥—ä–µ–º–Ω–æ—Å—Ç—å</p>
                                        <p class="subtitle is-5">{this.state.maxWeight}/500</p>
                                        <progress class="progress is-info" value={this.state.maxWeight} max={500} min={0}>{this.state.maxWeight}</progress>
                                    </div>
                                    <div className="box">
                                        <p class="title is-3">–£–ø—Ä–∞–≤–ª—è–µ–º–æ—Å—Ç—å</p>
                                        <p class="subtitle is-5">{this.state.control}/300</p>
                                        <progress class="progress is-info" value={this.state.control} max={300} min={0}>{this.state.control}</progress>
                                    </div>
                                    <div className="box">
                                        <p class="title is-3">–í–µ—Å</p>
                                        <p class="subtitle is-5">{this.state.weight}</p>
                                    </div>
                                    <div className="box">
                                        <p class="title is-3">–¶–µ–Ω–∞</p>
                                        <p class="subtitle is-5">{this.state.price}</p>
                                    </div>

                                    <div class="tags">
                                        {this.createDetailsTags()}
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="tile is-color3-light is-vertical">
                            <h1 class="title is-1">–î–µ—Ç–∞–ª–∏</h1>
                            <div class="field">
                                <div class="control">
                                    <div class="select is-medium">
                                        <select id="producerSelect" onChange={this.handleProducerChange}>
                                            {this.createProducersOptions()}
                                        </select>
                                    </div>

                                </div>
                            </div>
                            {this.createDetailsGroupsFromProducer(this.state.activeProducer)}
                        </div>
                    </div>
                </div >
            </div>
        );
    }
}

export default BoatConstructor;