import React from 'react';
import { Component } from 'react';
import BoatPropertyProgress from './BoatPropertyProgress';
import PropertyInfo from './PropertyInfo';

class BoatInfo extends Component {
    constructor(props) {
        super(props);

        this.state = { boat: '', details: [], speed: '', boost: '', maxWeight: '', control: '', weight: '', price: '' }

        this.state.boat = props.boat;
        this.state.details = props.details;
        this.countProperties = (comp) => {
            comp.state.details.forEach(function (detail) {
                comp.state.speed = +detail.speed + +comp.state.speed;
                comp.state.boost = +detail.boost + +comp.state.boost;
                comp.state.control = +detail.control + +comp.state.control;
                comp.state.weight = +detail.weight + +comp.state.weight;
                comp.state.price = +detail.price + +comp.state.price;
            });

            comp.state.maxWeight = +comp.state.boat.maxWeight;
            comp.state.speed = +comp.state.boat.speed + +comp.state.speed;
            comp.state.boost = +comp.state.boat.boost + +comp.state.boost;
            comp.state.control = +comp.state.boat.control + +comp.state.control;
            comp.state.weight = +comp.state.boat.weight + +comp.state.weight;
            comp.state.price = +comp.state.boat.price + +comp.state.price;
        }
    }

    createDetailsTags = () => {
        let tags = [];

        this.state.details.forEach(function (detail) {
            tags.push(<span class="tag is-warning is-medium">{detail.name}<button class="delete is-small"></button></span>);
        });
        console.log(tags);
        return tags;
    }

    render() {

        this.countProperties(this);


        return (
            <div>
                <div >
                    <figure class="image is-4by3">
                        <img src="https://bulma.io/images/placeholders/1280x960.png" alt="Placeholder image" />
                    </figure>
                </div>
                <div >
                    <h1 class="title is-1">–•–∞—Ä–∞–∫—Ç–µ—Ä–∏—Å—Ç–∏–∫–∏</h1>
                    <BoatPropertyProgress min='0' max='200' value={this.state.speed} name="–ú–∞–∫—Å–∏–º–∞–ª—å–Ω–∞—è —Å–∫–æ—Ä–æ—Å—Ç—å" />
                    <BoatPropertyProgress min='0' max='150' value={this.state.boost} name="–£—Å–∫–æ—Ä–µ–Ω–∏–µ" />
                    <BoatPropertyProgress min='0' max='60' value={this.state.maxWeight} name="–ì—Ä—É–∑–æ–ø–æ–¥—ä–µ–º–Ω–æ—Å—Ç—å" />
                    <BoatPropertyProgress min='0' max='10' value={this.state.control} name="–£–ø—Ä–∞–≤–ª—è–µ–º–æ—Å—Ç—å" />
                    <PropertyInfo name='–í–µ—Å' value={this.state.weight} />
                    <PropertyInfo name='–¶–µ–Ω–∞' value={this.state.price} />

                    <div class="tags">
                        {this.createDetailsTags()}
                    </div>
                </div>
            </div>
        );
    }
}

export default BoatInfo;