
import React from 'react';
import { Component } from 'react';
import { connect } from 'react-redux';
import Input from 'react-toolbox/lib/input';
import Button from 'react-toolbox/lib/button/Button';

class SignUpComponentToConnect extends Component {
    state = { username: '', password: '', error: '', didSignUp: false };

    handleChange = (name, value) => {
        this.setState({ ...this.state, [name]: value });
    };

    toLogin = () => this.props.dispatch({
        type: 'SET_REGISTRATION',
        value: false
    });

    setError = (msg) => {
        this.handleChange('error', msg);
    };

    signUp = () => {
        if (this.state.username === null || this.state.username.length < 1) {
            this.setError('–ù–µ–æ–±—Ö–æ–¥–∏–º–æ –≤–≤–µ—Å—Ç–∏ –∏–º—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è.');
            return;
        }
        else {
            if (reg.test(this.state.username) === false  & this.state.username.length > 
            1 & this.state.username != null){
               this.setError('–ù–µ–æ–±—Ö–æ–¥–∏–º–æ –≤–≤–µ—Å—Ç–∏ –∏–º—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –≤ —Ñ–æ—Ä–º–∞—Ç–µ email');
            return; 
            }
        }
        if (this.state.password === null || this.state.password.length < 1) {
            this.setError('–ù–µ–æ–±—Ö–æ–¥–∏–º–æ –≤–≤–µ—Å—Ç–∏ –ø–∞—Ä–æ–ª—å.');
            return;
        }

        fetch('/users/registration/new', { //–∞–¥—Ä–µ—Å—Å –Ω–∞ –±–µ–∫–µ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏ 
            method: 'POST',
            body: JSON.stringify({ username: this.state.username, password: this.state.password }) //–≤ –∫–∞–∫–æ–º —Ñ–æ—Ä–º–µ—Ç–µ –ø–µ—Ä–µ–¥–∞—Ç—å
        })
        .then((response) => {
            if (!response.ok)
                this.setError('–û—à–∏–±–∫–∞: –Ω–µ–≤–µ—Ä–Ω—ã–π –æ—Ç–≤–µ—Ç –æ—Ç —Å–µ—Ä–≤–µ—Ä–∞.');
            else {
                response.json().then((resp) => {
                    if (resp.success === "true") {
                        this.setError('');
                        this.handleChange('didSignUp', true);
                    }
                    else {
                        this.setError(resp.error);      //–æ—Ç–ª–æ–≤–∏—Ç—å illega exception —Å –±–µ–∫–∞ 
                    }
                }).catch((err) => {
                    this.handleChange('error', err.message);
                });

            }
        })
        .catch((error) => {
           this.setError("–ù–µ –ø–æ–ª—É—á–∏–ª–æ—Å—å –æ—Ç–ø—Ä–∞–≤–∏—Ç—å –∑–∞–ø—Ä–æ—Å: " + error.statusText);
        });
    };

    renderAfterSignUp() {
        return (
            <div>
                <h2>–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è —É—Å–ø–µ—à–Ω–æ –≤—ã–ø–æ–ª–Ω–µ–Ω–∞</h2>
                <Button onClick={this.toLogin} flat>–ü–µ—Ä–µ–π—Ç–∏ –∫ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏</Button>
            </div>
        );
    }

    render() {
        if (this.state.didSignUp)
            return this.renderAfterSignUp();

        return (
            <div>
                <form>
                    <b>–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è</b>
                    <Input type='email' label='Email address' id="user"  name="username" value={this.state.username}
                           onChange={this.handleChange.bind(this, 'username')} />
                    <Input type="password" id="pass" label="–ü–∞—Ä–æ–ª—å" name="password" value={this.state.password}
                           onChange={this.handleChange.bind(this, 'password')} />

                    
                    <span style={{color: "red"}}> {this.state.error} </span>
                   
                        <br/>
                    <Button className="button2"  raised primary style={{marginRight: '5px'}} onClick={this.signUp}>–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è</Button>
                    <Button className="button2" onClick={this.toLogin} flat>–ê–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—è</Button>
                </form>
            </div>
        );
    }
}
const reg = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;
const SignUpComponent = connect()(SignUpComponentToConnect);
export default SignUpComponent;