import React from 'react';
import { Component } from 'react';
import ReactDOM from 'react-dom';

class Tooltip extends React.Component {
constructor(props){
	super(props);
	this.toggle = this.toggle.bind(this);
	this.state = {opacity: false};
}

toggle(){
	const tooltipNode = ReactDOM.findDOMNode(this)
	this.setState({
		opacity: !this.state.opacity,
		top: tooltipNode.offsetTop,
		left: tooltipNode.offsetLeft,
	})

}

render(){
	const style = {
		zIndex: (this.state.opacity) ? 1000 : -1000,
		opacity: +this.state.opacity,
		top: (this.state.top || 0) + 20,
		left: (this.state.left || 0) - 30,
	}
		return (
					<div style= {{display: "inline"}}>
						<span style={{color: "blue"}}
						onMouseEnter={this.toggle}
						onMouseOut={this.toggle}
						>
						{this.props.children}
						</span>
						<div className="tooltip bottom" style={style}>
							<div className="tooltip-arrow"> </div>
							<div className="tooltip-inner" style= {{textAlign: "left"}}>
								–•–∞—Ä–∞–∫—Ç–µ—Ä–∏—Å—Ç–∏–∫–∏: <br/>
								–ú–∞–∫—Å–∏–º–∞–ª—å–Ω–∞—è —Å–∫–æ—Ä–æ—Å—Ç—å: {this.props.speedEffect} <br/>
								–£—Å–∫–æ—Ä–µ–Ω–∏–µ: {this.props.boostEffect} <br/>
								–£–ø—Ä–∞–≤–ª—è–µ–º–æ—Å—Ç—å: {this.props.controllabilityEffect} <br/>
								–í–µ—Å:  {this.props.weight} <br/>
								–¶–µ–Ω–∞: {this.props.price} 
							</div>
						</div>
					</div>

			);
	
}

}

export default Tooltip;