
import React from 'react';
import { Component } from 'react';
import { connect } from 'react-redux';
import Input from 'react-toolbox/lib/input';
import Button from 'react-toolbox/lib/button/Button';

class LoginComponentToConnect extends Component {
    state = { username: '', password: '', error: '' };

    handleChange = (name, value) => {
        this.setState({ ...this.state, [name]: value });
    };

    toSignUp = () => this.props.dispatch({
        type: 'SET_REGISTRATION',
        value: true
    });

    login = () => {
        if (this.state.username === null || this.state.username.length < 1) {
            this.handleChange('error', '–ù–µ–æ–±—Ö–æ–¥–∏–º–æ –≤–≤–µ—Å—Ç–∏ –∏–º—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è.');
            return;
        }
        if (this.state.password === null || this.state.password.length < 1) {
            this.handleChange('error', '–ù–µ–æ–±—Ö–æ–¥–∏–º–æ –≤–≤–µ—Å—Ç–∏ –ø–∞—Ä–æ–ª—å.');
            return;
        }

        fetch('j_security_check', {            //–∏–∑–º–µ–Ω–∏—Ç—å –∞–¥—Ä–µ—Å—Å –≤ —Å–æ–æ—å–≤–µ—Ç—Å—Ç–≤–∏–∏ —Å –±–µ–∫–æ–º 
            method: 'POST',
            body: JSON.stringify({ username: this.state.username, password: this.state.password }) //–≤ –∫–∞–∫–æ–º –≤–∏–¥–µ –ø–µ—Ä–µ–¥–∞—Ç—å 
        })
            .then((response) => {
                if (!response.ok)
                    this.handleChange('error', '–û—à–∏–±–∫–∞: –Ω–µ–≤–µ—Ä–Ω—ã–π –æ—Ç–≤–µ—Ç –æ—Ç —Å–µ—Ä–≤–µ—Ä–∞.');
                else {
                    response.json().then((resp) => {
                        if (resp.success === "true") {
                            this.handleChange('error', '');
                            window.location.href = "/competitions/all";            //—Å—Ç—Ä–∞–Ω–∏—á–∫–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª –Ω–∞–ø—Ä–∏–º–µ—Ä –ø—Ä–æ—Ñ–∏–ª—å –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è 
                        }
                        else {
                            this.handleChange('error', resp.error);
                        }
                    }).catch((err) => {
                        this.handleChange('error', err.message);
                    });
                }
            })
            .catch((error) => {
                this.handleChange('error', "–ù–µ –ø–æ–ª—É—á–∏–ª–æ—Å—å –æ—Ç–ø—Ä–∞–≤–∏—Ç—å –∑–∞–ø—Ä–æ—Å: " + error.statusText);
            });
    };

    render() {
        return (
            <div>
                <form>
                    <b>–ê–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—è</b><br />
                    <Input type='email' label='Email address' id="user" name="username" value={this.state.username}
                            onChange={this.handleChange.bind(this, 'username')} />
                    <Input type="password" id="pass" label="–ü–∞—Ä–æ–ª—å" name="password" value={this.state.password}
                            onChange={this.handleChange.bind(this, 'password')} />

                    
                    <span style={{color: "red"}}> {this.state.error} </span>
                     <br/>
                    <Button className="button1" raised primary style={{marginRight: '5px'}} onClick={this.login}>–í–æ–π—Ç–∏</Button>
                    <Button className="button2" onClick={this.toSignUp} flat>–ü–µ—Ä–µ–π—Ç–∏ –∫ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏</Button>
                </form>
            </div>
        );
    }
}

const LoginComponent = connect()(LoginComponentToConnect);
export default LoginComponent;
