import React from 'react';
import { Component } from 'react';
import { connect } from 'react-redux'; 
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import AllTopics from '../Components/AllTopicsComponent';
class AllSectionsComponent extends Component {

    state = {
        sections: []
    };

  
      constructor(props) {
        super(props);
        fetch('commands/forum/sections/all', {
            method: 'GET'
        })
            .then((response) => {
                if (!response.ok)
                    this.handleChange('error', '–û—à–∏–±–∫–∞: –Ω–µ–≤–µ—Ä–Ω—ã–π –æ—Ç–≤–µ—Ç –æ—Ç —Å–µ—Ä–≤–µ—Ä–∞.');
                else {
                    response.json().then((resp) => {
                        
                           this.state.sections = resp;
                           console.log(resp);
                           this.forceUpdate();
                        
                    }).catch((err) => {
                        console.log(err.message);
                    });
                }
            })
            .catch((error) => {
                console.log('–ù–µ –ø–æ–ª—É—á–∏–ª–æ—Å—å –æ—Ç–ø—Ä–∞–≤–∏—Ç—å –∑–∞–ø—Ä–æ—Å: ' + error.statusText);
            });
    }

    
   
    render() {
        let list = [];
        for (let i = this.state.sections.length -1; i >= 0; i--) {
            console.log(this.state.sections[i]);
            let p = this.state.sections[i];
            let link = "/webApp/topics/"+ p.sectionName + "/all" ;
            list.push (
                <li><Link to={link}>{p.sectionName}</Link></li>                         
            );

        }
               

        return (
            <div id="SectionsComponent">
                <ol className="pills">
                    {list}
                </ol>
            </div>
        );
    }
}

const Sections = connect((state) => state)(AllSectionsComponent);
export default AllSectionsComponent;