import React from 'react';
import { Component } from 'react';

class DetailsGroup extends Component {
    constructor(props) {
        super(props);
        this.state = { name: props.name, details: props.details }
    }

    createDetailsPics = () => {
        let detailsPics = []

        this.state.details.forEach(detail => {
            let detailCard =<div class="tile is-1"><figure class="image is-128x128"><img src="https://dfg.ng" /></figure></div>;

            detailsPics.push(detailCard);

        });

        return detailsPics;

    }

    render() {
        return (
            <div className="box">
                <p class="title is-3">{this.state.name}</p>
                <div class="tile is-ancestor">
                    {this.createDetailsPics()}
                </div>
            </div>
        );
    }
}

export default DetailsGroup;