import React from 'react';
import { Component } from 'react';

class BoatPropertyProgress extends Component {
    constructor(props) {
        super(props);

        this.state = { min: props.min, max: props.max, value: props.value, name: props.name }

    }

    render() {

        return (
            <div className="box">
                <p class="title is-3">{this.state.name}</p>
                <p class="subtitle is-5">{this.state.value}/{this.state.max}</p>
                <progress class="progress is-info" value={this.state.value} max={this.state.max} min={this.state.min}>{this.state.value}</progress>
            </div>
        );
    }

}

export default BoatPropertyProgress;