import React from 'react';
import { Component } from 'react';

class Line extends Component
{
    constructor(props) 
    {
		super(props);
        this.label = props.label;
        this.text = props.text;
    }

    render()
    {
        return(
            <div className="profileUserInfoLine">
                <div className="heading is-color3-light">
                    {this.label}
                </div>

                <div className="title is-color3-light">
                    {this.text}
                </div>
            </div>
        );
    }
}

export default Line;