import React from 'react';
import ReactDOM from 'react-dom';
import { Component } from 'react';
import { Provider } from 'react-redux';
import { createStore, applyMiddleware } from 'redux';
import { render } from 'react-dom';
import { Switch } from 'react-router';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import StartPage from '../src/Components/StartPage';
import Snackbar from 'react-toolbox/lib/snackbar/Snackbar';
import Main from '../src/Components/Main';
import ForumSections from '../src/Components/ForumSectionsComponent';
import ForumTopics from '../src/Components/ForumTopicsComponent';
import ForumRecords from '../src/Components/ForumRecordsComponent';
import Message from '../src/Components/MessageComponent';
import Dialog from '../src/Components/DialogWithUsers';
import Profile from "./Components/Profile/Profile";
import BoatConstructor from "./Components/BoatConstructor/BoatConstructor";
import './App.css';

class App extends Component {

  render() {

    return (
      <div>
        <Router>
          <Switch>
            <Route path="/webApp/dialog" component={Dialog} />
            <Route path="/webApp/message" component={Message} />
            <Route path="/webApp/records" component={ForumRecords} />
            <Route path="/webApp/topics" component={ForumTopics} />
            <Route path="/webApp/sections" component={ForumSections} />
            <Route path="/webApp/users/profile" component={Profile} />
            <Route path="/webApp/boats" component={BoatConstructor} />
            <Route path="/" component={Main} />
          </Switch>
        </Router>
      </div>
    )
  }
}

export default App;
