import React from 'react';
import { Component } from 'react';
import { connect } from 'react-redux';
class RatingComponent extends Component {

    state = {
        users: []
    };

  
      constructor(props) {
        super(props);
        fetch('commands/users/all', {
            method: 'GET'
        })
            .then((response) => {
                if (!response.ok)
                    this.handleChange('error', '–û—à–∏–±–∫–∞: –Ω–µ–≤–µ—Ä–Ω—ã–π –æ—Ç–≤–µ—Ç –æ—Ç —Å–µ—Ä–≤–µ—Ä–∞.');
                else {
                    response.json().then((resp) => {
                        this.state.users = resp;
                        console.log(resp);
                        this.forceUpdate();
                    }).catch((err) => {
                        console.log(err.message);
                    });
                }
            })
            .catch((error) => {
                alert('–ù–µ –ø–æ–ª—É—á–∏–ª–æ—Å—å –æ—Ç–ø—Ä–∞–≤–∏—Ç—å –∑–∞–ø—Ä–æ—Å: ' + error.statusText);
            });
    }

    
   
    render() {

        function compare(a,b) {
            if (a.rate > b.rate)
                return -1;
            if (a.rate < b.rate)
                return 1;
            return 0;
            }

        let sortable = [];
        sortable = this.state.users.slice();
        
        sortable = sortable.sort(compare);
        let table = [];
        for (let i = 0; i < sortable.length; i++) {                               {/* –∏–∑–º–µ–Ω–∏—å —á–∏—Å–ª–æ*/}         
            console.log(sortable[i]);
            let p = sortable[i];
            table.push (<tr>
                <td>{p.name}</td>                              {/* —É—Ç–æ—á–Ω–∏—Ç—å –≤ –∫–∞–∫–æ–º —Ñ–æ—Ä–º–∞—å–µ –≤–µ—Ä–Ω–∫—Ç—Å—è json*/}
                <td>{p.rate}</td>
                <td>{p.sportRate}</td>
            </tr>);

        }
               

        return (
            <div id="RaitingComponent">
                <table className="table_blur">
                    <tr>
                        <th>–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å</th>
                        <th>–†–µ–π—Ç–∏–Ω–≥</th>
                        <th>–°–ø–æ—Ä—Ç–∏–≤–Ω—ã–π —Ä–∞–∑—Ä—è–¥</th>
                    </tr>
                    {table}
                </table>
            </div>
        );
    }
}

const Rating = connect((state) => state)(RatingComponent);
export default RatingComponent;

