
import AuthComponent from '../Components/AuthComponent';
import HeaderComponent from '../Components/HeaderComponent';
import Header from '../Components/Header';
import React from 'react';
import { Component } from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { createStore, applyMiddleware } from 'redux';


const initialState = { isRegistration: false, errorMsg: '', successMsg: '' };
const authStore = createStore(authForm, initialState);
function authForm(state, action) {
    let newState = Object.assign({}, state);

    switch (action.type) {
        case 'SET_REGISTRATION':
            newState.isRegistration = action.value;
            newState.errorMsg = '';
            newState.successMsg = '';
            return newState;
        case 'SET_ERROR':
            newState.errorMsg = action.value;
            newState.successMsg = '';
            return newState;
        case 'SET_SUCCESS':
            newState.successMsg = action.value;
            newState.errorMsg = '';
            return newState;
        default:
            return newState;
    }
}
