import React from 'react';
import { Component } from 'react';
import { connect } from 'react-redux'; 
import Button from 'react-toolbox/lib/button/Button';
import update from 'immutability-helper'
import Modal from 'react-modal';
import Avatar from '../Components/AvatarComponent'; 
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import utocka from './utocka.png';

const customStyles = {
  content : {
    top                   : '50%',
    left                  : '50%',
    right                 : 'auto',
    bottom                : 'auto',
    marginRight           : '-50%',
    transform             : 'translate(-50%, -50%)',
    width                 : '300px',
    height                : '200px'
  }
};

class UsertoMassageComponent extends Component {

    state = {

        users: [],

        modalrecordIsOpen: false,

        tomail: "",
       
    };

  
      constructor(props) {
        super(props);
       
       
        
        fetch('commands/users/all', {
            method: 'GET'
        })
            .then((response) => {
                if (!response.ok)
                    this.handleChange('error', '–û—à–∏–±–∫–∞: –Ω–µ–≤–µ—Ä–Ω—ã–π –æ—Ç–≤–µ—Ç –æ—Ç —Å–µ—Ä–≤–µ—Ä–∞.');
                else {
                    response.json().then((resp) => {
                        this.state.users = resp;
                        console.log(resp);
                        this.forceUpdate();
                    }).catch((err) => {
                        console.log(err.message);
                    });
                }
            })
            .catch((error) => {
                alert('–ù–µ –ø–æ–ª—É—á–∏–ª–æ—Å—å –æ—Ç–ø—Ä–∞–≤–∏—Ç—å –∑–∞–ø—Ä–æ—Å: ' + error.statusText);
            });
    }

    openModalmessage() {
    this.setState({modalrecordIsOpen: true});
    };

    closeModalmessage() {
    this.setState({modalrecordIsOpen: false});
    };

    settomaild(mail) {
         
         this.setState({modalrecordIsOpen: true});
         this.setState({tomail: mail});
    }

    sendmessage = () => { 
        let mail = this.state.tomail;
        let text = document.getElementById("newRecord").value;
        let path = "commands/messages/send/" + mail;    {/*–ø—Ä–∞–≤–∏–ª—å–Ω–æ –ª–∏ path */}

        let data = new FormData();

        data.set("message", text);

       this.closeModalmessage();
        fetch(path, {
            method: 'POST',
            body: data   //? —á—Ç–æ –ø–µ—Ä–µ–¥–∞—Ç—å –≤ body 
        })
            .then((response) => {
                if (!response.ok)
                    console.log('–û—à–∏–±–∫–∞: –Ω–µ–≤–µ—Ä–Ω—ã–π –æ—Ç–≤–µ—Ç –æ—Ç —Å–µ—Ä–≤–µ—Ä–∞.');
                else {
                    response.json().then((resp) => {
                        this.closeModalmessage();
                        this.forceUpdate();
                        
                    }).catch((err) => {
                        console.log('json parse error: ' + err);
                    });
                }
            })
            .catch((error) => {
                console.log("–ù–µ —É–¥–∞–ª–æ—Å—å –æ—Ç–ø—Ä–∞–≤–∏—Ç—å –∑–∞–ø—Ä–æ—Å: " + error);
            });
        

   
    };

   
    render() {
        let table = [];
        let list = [];
        for (let i = this.state.users.length -1; i >= 0; i--) {
            console.log(this.state.users[i]);
            let p = this.state.users[i];
            let user = "/webApp/avatar_" + p.id + ".png";
            let link = "/webApp/dialog/"+ p.email; 
            let mail = p.email;
             if ((w>=1192)|| ((w>= 750) && (w <= 1191))) {
            table.push(<tr>
             
                <td class="white_space"> 
                    <div id = "messageuser">
                    <div class = "mesimg">
                    <Avatar userid = {user}/>
                    </div>
                    <div class = "mesuser">
                    <ul class = "beads white_space">                     
                     <li class="white_space">  
                         <Link to={link}>{p.name}</Link>
                         </li>
                     </ul>
                     </div>
                </div>
               </td>
                <td class="white_space"> <Button onClick={(mail) => this.settomaild(p.id)}>–ù–∞–ø–∏—Å–∞—Ç—å —Å–æ–æ–±—â–µ–Ω–∏–µ</Button> </td>

                </tr>);
        }

        else 
        {
            if (w<750) {
                table.push(<tr>
             
                <td class="white_space"> 
                    <div id = "messageuser">
                    <div class = "mesimg">
                    <Avatar userid = {user}/>
                    </div>
                    <div class = "mesuser">
                    <ul class = "beads">                     
                     <li>  <Link to={link}>{p.name}</Link></li>
                     </ul>
                     </div>
                </div>
               </td>
                <td class="white_space"> <Button onClick={(mail) => this.settomaild(p.id)}>–ù–∞–ø–∏—Å–∞—Ç—å</Button> </td>

                </tr>);
            }
        }
        }
               

        return ([
            <div id="MessageComponent">
                 <table className="table_blur">
                     <tr>
                        <th>–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å</th>
                        <th>–û—Ç–ø—Ä–∞–≤–∏—Ç—å</th>
                    </tr>
                    {table}
                </table>
            </div>,

            <div id ="Model">
        <Modal
          isOpen={this.state.modalrecordIsOpen}
          onRequestClose={this.closeModalmessage.bind(this)}
          style={customStyles}
          contentLabel="SendMessage Modal">

        <div>–û—Ç–ø—Ä–∞–≤—å—Ç–µ —Å–≤–æ–µ —Å–æ–æ–±—â–µ–Ω–∏–µ</div>
          <form>
           <textarea name="record" id="newRecord"></textarea>                              {/*neme –¥–ª—è –∏–¥–µ–Ω—Ç–µ—Ñ–∏–∫–∞—Ü–∏–∏ –ø–æ–ª—è*/}
            <Button onClick={this.sendmessage}>–û—Ç–ø—Ä–∞–≤–∏—Ç—å</Button>
          </form> 
          </Modal>
        </div>,
       ] );
    }
}
const w = window.screen.width;
const UsertoMassage = connect((state) => state)(UsertoMassageComponent);
export default UsertoMassageComponent;