import React from 'react';
import { Component } from 'react';
import { connect } from 'react-redux';
import Avatar from '../Components/AvatarComponent';
import Button from 'react-toolbox/lib/button/Button';
import update from 'immutability-helper';
import Modal from 'react-modal';





const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
        width: '300px',
        height: '200px'
    }
};


class ComentstoRecordComponent extends Component {

    state = {
        coments: [],

        nowuser: [],


        modalchangecomIsOpen: false,

        changecom: "",

        changecomid: "",

        changei: "",

        user: "",

    };


    constructor(props) {
        super(props);
        let path = this.props.recordid;
        path = "/webApp/commands/forum/records/" + path + "/all";

        fetch(path, {
            method: 'GET'
        })
            .then((response) => {
                if (!response.ok)
                    this.handleChange('error', '–û—à–∏–±–∫–∞: –Ω–µ–≤–µ—Ä–Ω—ã–π –æ—Ç–≤–µ—Ç –æ—Ç —Å–µ—Ä–≤–µ—Ä–∞.');
                else {
                    response.json().then((resp) => {

                        this.state.coments = resp;
                        this.forceUpdate();

                    }).catch((err) => {
                        console.log(err.message);
                    });
                }
            })
            .catch((error) => {
                console.log('–ù–µ –ø–æ–ª—É—á–∏–ª–æ—Å—å –æ—Ç–ø—Ä–∞–≤–∏—Ç—å –∑–∞–ø—Ä–æ—Å: ' + error.statusText);
            });



        fetch('/webApp/commands/users/profile/info', {
            method: 'GET'
        })
            .then((response) => {
                if (!response.ok)
                    this.handleChange('error', '–û—à–∏–±–∫–∞: –Ω–µ–≤–µ—Ä–Ω—ã–π –æ—Ç–≤–µ—Ç –æ—Ç —Å–µ—Ä–≤–µ—Ä–∞.');
                else {
                    response.json().then((resp) => {

                        this.state.nowuser = resp;
                        this.forceUpdate();

                    }).catch((err) => {
                        console.log(err.message);
                    });
                }
            })
            .catch((error) => {
                console.log('–ù–µ –ø–æ–ª—É—á–∏–ª–æ—Å—å –æ—Ç–ø—Ä–∞–≤–∏—Ç—å –∑–∞–ø—Ä–æ—Å: ' + error.statusText);
            });


        let newcoment = this.props.newcoment;
        this.state.coments = this.state.coments.push(newcoment);
    }

    openModalchangecom() {
        this.setState({ modalchangecomIsOpen: true });
    };

    closeModalchangecom() {
        this.setState({ modalchangecomIsOpen: false });
    };



    settochangecom(comentid, i, userid) {
        this.setState({ changecomid: comentid });
        this.setState({ changei: i });
        this.setState({ user: userid });
        this.setState({ modalchangecomIsOpen: true });
    }

    removeItem(i) {                                                               //–º–æ–∂–µ—Ç–Ω–µ –ø–æ–Ω–∞–¥–æ–±–∏—Ç—Å—è —Ç.–∫ –ø—Ä–∏ –∑–∞–ø—Ä–æ—Å–µ –Ω–∞ —Å–µ—Ä–≤–µ—Ä –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∏ –æ–±–Ω–∞–≤–∏—Ç—Å—è state 
        this.setState((prevState) => ({
            records: update(prevState.records, { $splice: [[i, 1]] })
        }))
    };

    delcoment = (comentid, i, userid) => {
        let p = this.state.nowuser;
        let nowuserid = p.id;
        let nowgroup = p.groups[0];
       

            fetch('/webApp/commands/forum/records/delete/comment/' + comentid, {
                method: 'POST'  //? —á—Ç–æ –ø–µ—Ä–µ–¥–∞—Ç—å –≤ body 
            })
                .then((response) => {
                    if (!response.ok)
                        alert('–û—à–∏–±–∫–∞: –Ω–µ–≤–µ—Ä–Ω—ã–π –æ—Ç–≤–µ—Ç –æ—Ç —Å–µ—Ä–≤–µ—Ä–∞.');
                    else {
                        response.json().then((resp) => {

                            this.removeItem(i);
                            this.forceUpdate();

                        }).catch((err) => {
                            console.log('json parse error: ' + err);
                            this.removeItem(i);
                            this.forceUpdate();
                        });
                    }
                })
                .catch((error) => {
                    console.log("–ù–µ —É–¥–∞–ª–æ—Å—å –æ—Ç–ø—Ä–∞–≤–∏—Ç—å –∑–∞–ø—Ä–æ—Å: " + error);
                });

        
    };



    changecoment = () => {
        let change = document.getElementById('changeTextArea1').value;
        this.setState({ changecom: change });
        let p = this.state.nowuser;
        let nowuserid = p.id;
        let nowgroup = p.groups[0];

        let data = new FormData();

        data.set("newCommentText", change);

        

            fetch('/webApp/commands/forum/records/change/comment/' + this.state.changecomid, {
                method: 'POST',
                body: data     //? —á—Ç–æ –ø–µ—Ä–µ–¥–∞—Ç—å –≤ body 
            })
                .then((response) => {
                    if (!response.ok)
                        alert('–û—à–∏–±–∫–∞: –Ω–µ–≤–µ—Ä–Ω—ã–π –æ—Ç–≤–µ—Ç –æ—Ç —Å–µ—Ä–≤–µ—Ä–∞.');
                    else {
                        response.json().then((resp) => {

                            let i = this.state.changei;
                            let g = this.state.coments[i];
                            g.coment = this.state.changecom;
                            this.closeModalchangecom();
                            this.forceUpdate();
                        }).catch((err) => {
                            console.log('json parse error: ' + err);
                            this.closeModalchangecom();
                            this.forceUpdate();
                        });
                    }
                })
                .catch((error) => {
                    alert("–ù–µ —É–¥–∞–ª–æ—Å—å –æ—Ç–ø—Ä–∞–≤–∏—Ç—å –∑–∞–ø—Ä–æ—Å: " + error);
                });
        
    };


    render() {

        let table = [];
        let headline = [];
        let headlineuser = [];
        let open = this.props.open;
        for (let i = this.state.coments.length - 1; i >= 0; i--) {
            console.log("comment",this.state.coments[i]);
            let p = this.state.coments[i];
            let user = "/imgUpload/avatar_" + p.author.id + ".png";
            let coment = p.id;
            let userid = p.author.id;
            if (w >= 1192) {
                table.push(<tr>
                    <td>
                        <tr><Avatar userid={user} /></tr>
                        <tr>{p.author.name} </tr>
                    </td>
                    <td>
                        <div class="white_space1">
                            <tr class="white_space">{p.message}</tr>
                        </div>
                    </td>
                    <td>
                        <tr> <Button class="sbut" onClick={() => this.delcoment(coment, i, userid)}>–£–¥–∞–ª–∏—Ç—å –∫–æ–º–µ–Ω—Ç–∞—Ä–∏–π</Button></tr>
                        <tr> <Button class="sbut" onClick={() => this.settochangecom(coment, i, userid)}>–ò–∑–º–µ–Ω–∏—Ç—å –∫–æ–º–µ–Ω—Ç–∞—Ä–∏–π</Button></tr>
                    </td>
                </tr>);
            }
            else {
                if ((w >= 750) && (w <= 1191)) {
                    table.push(<tr>
                        <td>

                            <tr>{p.author.name} </tr>
                        </td>
                        <td>
                            <tr>
                                <Button class="sbut" onClick={() => this.delcoment(coment, i, userid)}>–£–¥–∞–ª–∏—Ç—å –∫–æ–º–µ–Ω—Ç–∞—Ä–∏–π</Button> <br />
                                <Button class="sbut" onClick={() => this.settochangecom(coment, i, userid)}>–ò–∑–º–µ–Ω–∏—Ç—å –∫–æ–º–µ–Ω—Ç–∞—Ä–∏–π</Button>
                            </tr>
                            <div class="white_space1">
                                <tr class="white_space">{p.message}</tr></div>
                        </td>
                    </tr>);
                }
                else {
                    if (w < 750) {
                        table.push(<tr>
                            <td>
                                <tr>
                                    <Button class="sbut" onClick={() => this.delcoment(coment, i, userid)}>–£–¥–∞–ª–∏—Ç—å –∫–æ–º–µ–Ω—Ç–∞—Ä–∏–π</Button> <br />
                                    <Button class="sbut" onClick={() => this.settochangecom(coment, i, userid)}>–ò–∑–º–µ–Ω–∏—Ç—å –∫–æ–º–µ–Ω—Ç–∞—Ä–∏–π</Button>
                                </tr>
                                <div class="white_space1">
                                    <tr class="white_space">{p.message}</tr></div>
                            </td>
                        </tr>);
                    }
                }
            }
        }

        if (w >= 1192) {
            headline.push(<th>–î–µ–π—Å—Ç–≤–∏–µ</th>);
            headlineuser.push(<th>–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å</th>)
        }


        if ((w >= 750) && (w <= 1191)) {
            headlineuser.push(<th>–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å</th>);
        }


        console.log(table);
        if (open == true) {

            return ([
                <div id="commentsinrecord">
                    <table className="table_blur">
                        <caption>–ö–æ–º–µ–Ω—Ç–∞—Ä–∏–∏ </caption>
                        <tr>
                            {headlineuser}
                            <th>–ö–æ–º–µ–Ω—Ç–∞—Ä–∏–π</th>
                            {headline}
                        </tr>
                        {table}
                    </table>
                </div>,

                <div id="Model3">
                    <Modal
                        isOpen={this.state.modalchangecomIsOpen}
                        onRequestClose={this.closeModalchangecom.bind(this)}
                        style={customStyles}
                        contentLabel="ChangeCom Modal">

                        <div>–ò–∑–º–µ–Ω–∏—Ç—å –∫–æ–º–µ–Ω—Ç–∞—Ä–∏–π</div>
                        <form>
                            <textarea id="changeTextArea1" name="changerec"></textarea>                              {/*neme –¥–ª—è –∏–¥–µ–Ω—Ç–µ—Ñ–∏–∫–∞—Ü–∏–∏ –ø–æ–ª—è*/}
                            <Button onClick={this.changecoment}>–ò–∑–º–µ–Ω–∏—Ç—å –∫–æ–º–µ–Ω—Ç–∞—Ä–∏–π</Button>
                        </form>
                    </Modal>
                </div>

            ]
            );

        }

        else {
            return (

                <div> </div>

            );
        }
    }
}
const w = window.screen.width;
const ComentstoRecord = connect((state) => state)(ComentstoRecordComponent);
export default ComentstoRecordComponent;