import React from 'react';
import { Component } from 'react';
import Input from 'react-toolbox/lib/input';
import Button from 'react-toolbox/lib/button/Button';
import { Redirect } from "react-router-dom";

class ProfileEdit extends Component {
    constructor(props) {
        super(props);
        this.user = props.user;
        this.state = { name: this.user.name, gender: this.user.gender, email: this.user.email, password: '', sportRate: this.user.sportRate, toProfileInfo: false, birthday: this.user.birthday };
    }

    handleChange = (name, value) => {
        this.setState({ ...this.state, [name]: value });
    };

    cancelEditing = () => {
        this.state.toProfileInfo = true;
        this.forceUpdate();
    }

    saveChanges = () => {

        let data = new FormData();

        data.set("name", this.state.name);
        data.set("gender", this.state.gender);
        data.set("password", this.state.password);

        fetch("/webApp/commands/users/profile/change",
            {
                method: 'post',
                body: data
            }
        )
            .then((response) => {
                if (!response.ok)
                    this.handleChange('error', '–û—à–∏–±–∫–∞: –Ω–µ–≤–µ—Ä–Ω—ã–π –æ—Ç–≤–µ—Ç –æ—Ç —Å–µ—Ä–≤–µ—Ä–∞.');
                else {
                    
                        this.state.toProfileInfo = true;
                        this.forceUpdate();
                    
                    };
                })
            .catch((error) => {
                console.log('–ù–µ –ø–æ–ª—É—á–∏–ª–æ—Å—å –æ—Ç–ø—Ä–∞–≤–∏—Ç—å –∑–∞–ø—Ä–æ—Å: ' + error.statusText);
            });
    }

    render() {

        if (this.state.toProfileInfo === true)
            return (<Redirect to='/webApp/' />);

        return (
            <div id="profileEdit" className="is-color3">
                <section>
                    <Input type='text' label='–ò–º—è' name='name' value={this.state.name} required onChange={this.handleChange.bind(this, 'name')} maxLength={16} />
                    <Input type='text' label='–ü–æ–ª' name='gender' value={this.state.gender} required onChange={this.handleChange.bind(this, 'gender')} maxLength={1} />
                    <Input type='password' label='–ü–∞—Ä–æ–ª—å' name='password' value={this.state.password} required onChange={this.handleChange.bind(this, 'password')} />
                </section>

                <Button label='–°–æ—Ö—Ä–∞–Ω–∏—Ç—å' raised primary onClick={this.saveChanges} />
                <Button label='–û—Ç–º–µ–Ω–∏—Ç—å' onClick={this.cancelEditing} raised />
            </div>
        );
    }

}

export default ProfileEdit;